/* Menu on right side of header */
.header-right {
    position: fixed;
    top: 0;
    right: 0;
    white-space: nowrap;
    text-shadow: 0 0 20px black;
    
    display: inline-flex;
    flex-wrap: nowrap;

    z-index: 2;
}
