        height: 12vh;
        width: 100%;
        margin-right: 20px;

        flex-direction: row;
        justify-content: space-evenly;

        -webkit-animation-name: header-entrance;

                animation-name: header-entrance;
        -webkit-animation-duration: 1.2s;
                animation-duration: 1.2s;
    }
}
@media (max-width: 499px) {
    .header-right {
        height: 0;
        padding-right: 5%;
        padding-left: 100%;
